module.exports = Chart;

function Chart(config) {

    let self = this;

    /*  */
    self.supportChartType = ['tick', 'candle'];

    /*   */
    // self.numOfVertical = config.numOfVerLine || 9; //
    // self.numOfHorizental = config.numOfHorLine || 10; //

    /*   */
    /*  */
    self.candle = { //
        m1: {
            min: 1,
            period: 60000 // 1
        },
        m5: {
            min: 5,
            period: 300000 // 5
        },
        m15: {
            min: 15,
            period: 900000 // 15
        },
        m30: {
            min: 30,
            period: 1800000 // 30
        }
    };

    /*  */
    /*
     *  {
     *    canvasId: xxx,
     *    canvasContainerId: xxx,
     *    chartType: candle, //
     *    candlePeriod: m1,
     *    currency: EURUSD,
     *    _crosson: bool //
     *    _candleTimeInterval: 5 //  5
     *    _candleMargin: 3
     *    zoom: 5 // 
     *  }
     * */
    self.config = {
        _candleTimeInterval: 5,
        _candleMargin: 2,
        _boxHeight:38,
        zoom: 3,
        /* config of indicates */
        indi: {
            verlineon: false
        },
        candleRiseColor:'#3d63ae',
        candleFallColor:'#d22c39',
        croseLableColor:'#000',//
        croseLableOpacity:'0.8',//
        croseLineColor:'#000',//
        croseLineOpactiy:'0.4',//
        currentPriceLabelColor:'#000',//
        currentPriceLabelOpacity:'1',//
        currentPriceLineColor:'#000'
    };

    /*  */
    self.historyPrice = [];

    /*  */
    self.candleHistoryPrice = [];

    /*
       
       leftright
       topbottom
       */
    self.scaleLeft = []; // [point, time]
    self.scaleRight = []; // [point, time]
    self.scaleTop = []; // [point, price]
    self.scaleBottom = []; // [point, price]

    /* [time price] */
    self.currentPricePair = [];

    /* [[time,bid,ask],......] */
    self.nowChartPrice = [];
    /*    */
    self.chartContext = null;
    self.iChartContext = null;

    /*  */
    self.crossLinePoint = [];

    self.indicators = {
        verLine: null, // point x
        horLine: null // point y
    };

    self._initChart(config);
}



